import Vue from 'vue'
import Router from 'vue-router'

import leftMenu from '@/components/leftMenu';
import top from '@/components/top';
import Index from '@/components/Index'
import tradingQuotations from '@/components/tradingQuotations'
import fahuo from '@/components/fahuo'
import cheyuanInfo from '@/components/cheyuanInfo'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      components: {
        content: Index
      }
    },
    {
      path: '/tradingQuotations',
      name: '交易行情',
      components: {
        left: leftMenu,
        content: tradingQuotations,
        top: top
      }
    },
    {
      path: '/fahuo',
      name: '我要发货',
      components: {
        left: leftMenu,
        content: fahuo,
        top: top
      }
    },
    {
      path: '/cheyuanInfo',
      name: '车源信息',
      components: {
        left: leftMenu,
        content: cheyuanInfo,
        top: top
      }
    }
  ]
})



